.sidebar-content {
  counter-reset: step;
  padding: 0px;
}

.sidebar-content li {
  list-style-type: none;
  position: relative;
  height: 50px;
  margin: 30px 0px !important;
  font-family: 'Omnes-medium';
  border-radius: 4px;
  padding: 12px;
  color: #80868B;
}

.stepLink {
  color: #80868B;
}

.sidebar-content li:active {
  background-color: #DADCE0;
  color: #000000;
}

.sidebar-content li:before {
  content: counter(step);
  counter-increment: step;
  width: 30px;
  height: 30px;
  background:#80868B;
  display: inline-block;
  text-align: center;
  line-height: 30px;
  margin-right: 10px;
  border-radius: 50%;
  font-family: 'Omnes-light';
  color: #ffffff;
}

.sidebar-content li:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 3px;
  background-color: #80868B;
  left: 25.3px;
  top: -38px
}

.sidebar-content li:first-child:after {
  content: none;
}

.sidebar-content li.active, .sidebar-content li.active > a {
  color: #1B2F57;
  background-color: #DADCE0;
}

.sidebar-content li.active:before, .sidebar-content li.active:after {
  background-color: #55B748;
}

.sidebar-content li.lessThan > a {
  color: #1B2F57;
}

.sidebar-content li.lessThan:after, .sidebar-content li.lessThan:before {
  background-color: #55B748;
}

